import React from "react";
import { FormControlLabel, Checkbox, Typography } from "@material-ui/core";
import { GridOn, GridOff } from "@material-ui/icons";

const ProjectControlsView = (props) => {
  return (
    <FormControlLabel
      control={
        <Checkbox
          size="small"
          color={props.checked ? "primary" : "default"}
          icon={<GridOff />}
          checkedIcon={<GridOn />}
          checked={props.checked}
          onChange={props.onChange}
        />
      }
      label={
        <Typography
          variant="subtitle2"
          color={props.checked ? "primary" : "textSecondary"}
          component="span"
        >
          Grid
        </Typography>
      }
    />
  );
};

export default ProjectControlsView;
