import React, { useState } from "react";
import { motion } from "framer-motion";
import { Grid, Box, Typography } from "@material-ui/core";
import { varWrapBoth } from "../../utilities";

import {
  ProjectItem,
  ProjectFilterByName,
  ProjectControlsSort,
  ProjectControlsView,
} from "..";
//
import { useFormik } from "formik";
import { CheckBoxes } from "./controls-checkboxes";

const ProjectList = (props) => {
  let {
    projects,
    girdView,
    sort,
    onToogleView,
    onToogleSort,
    deleteProject,
  } = props;

  const [filter, setFilter] = useState({
    byName: "",
    byCategory: "",
  });

  const formik = useFormik({
    initialValues: {
      checkList: [],
    },
  });

  // FILLTER
  const handleChangeFilter = (e) => {
    const { name, value } = e.target;
    setFilter({ ...filter, [name]: value });
  };

  function categoryLength(value) {
    const count = projects.filter((item) => {
      return item.category === value;
    });
    return count.length;
  }

  const filterByy = projects
    .map((e) => e["category"])
    .map((e, i, final) => final.indexOf(e) === i && i)
    .filter((e) => projects[e])
    .map((e) => projects[e]);

  const filterOption = filterByy.map((item) => {
    return {
      _id: item._id,
      category: item.category,
      length: categoryLength(item.category),
    };
  });

  if (filter.byName) {
    projects = projects.filter((item) => {
      return (
        item.name.toLowerCase().indexOf(filter.byName.toLowerCase()) !== -1
      );
    });
  }

  if (filter.byCategory) {
    projects = projects.filter((item) => {
      return item.category === filter.byCategory;
    });
  }

  if (formik.values.checkList.length > 0) {
    projects = projects.filter((item) => {
      return formik.values.checkList.includes(item.category);
    });
  }

  // console.log("filterByy", JSON.stringify(filterByy, null, 2));

  return (
    <>
      <Box mt={4} mb={8}>
        {/********** FILTERS ***********/}
        <Box position="relative">
          <ProjectFilterByName
            value={filter.byName}
            onChange={handleChangeFilter}
          />

          <Box position="absolute" top={11} right={8}>
            <CheckBoxes
              projects={projects}
              formik={formik}
              options={filterOption}
            />
          </Box>
        </Box>

        {/********** RESULT ***********/}
        <Box mt={2}>
          {filter.byName ||
          filter.byCategory ||
          formik.values.checkList.length > 0 ? (
            <Typography variant="subtitle2">
              {projects.length}{" "}
              <Typography
                variant="subtitle2"
                color="textSecondary"
                component="span"
              >
                results found
              </Typography>
            </Typography>
          ) : null}
        </Box>

        <Box
          mt={2}
          display="flex"
          justifyContent="flex-end"
          alignItems="center"
        >
          {/********** TOGGLE VIEW ***********/}
          <ProjectControlsView checked={girdView} onChange={onToogleView} />

          {/********** TOGGLE SORT ***********/}
          <Box mx={2}>
            <ProjectControlsSort checked={sort} onChange={onToogleSort} />
          </Box>
        </Box>
      </Box>

      {/********** SHOW LIST ***********/}
      <motion.div variants={varWrapBoth}>
        <Grid container spacing={girdView ? 4 : null}>
          {projects.map((item, index) => (
            <ProjectItem
              key={item._id}
              item={item}
              index={index}
              deleteProject={deleteProject}
              girdView={girdView}
            />
          ))}
        </Grid>
      </motion.div>
    </>
  );
};

export default ProjectList;
